body {
  margin: 10px;
}

.inputs {
  display: flex;
  flex-direction: column;
  margin: 10px 0;
}

.inputs input {
  width: 300px;
}

.userList {
  display: flex;
  flex-direction: row;
  margin: 30px 0px;
  flex-wrap: wrap;
}

.user {
  display: flex;
  flex-direction: row;
  border: solid 2px black;
  padding: 5px;
  margin: 0 10px 10px 0;
}

.userInfo {
  display: flex;
  flex-direction: column;
}

.userName {
  margin-bottom: 10px;
}
